import React from "react"
import { graphql, Link } from "gatsby"

const IndexPage = ({ data }) => {
  const { edges: posts } = data.allMarkdownRemark

  return (
    <div>
      <div class="navbar mb-2 shadow-lg bg-neutral text-neutral-content">
        <a class="btn btn-ghost normal-case text-xl">Tyson Ross</a>
      </div>
      <ol class="flex flex-col items-center ml-2 mt-3 space-y-4">
        {posts.map(({ node: post }) => (
            <li class="card w-96 shadow-xl" key={post.id}>
              <Link to={post.frontmatter.path}>
              <div class="card-body">
                <h2 class="card-title">{post.frontmatter.title}</h2>
              </div>
            </Link>
          </li>
        ))}
      </ol>
    </div>
  )
}

export default IndexPage

export const pageQuery = graphql`
  query {
    allMarkdownRemark(
      filter: { frontmatter: { title: { ne: null }, path: { ne: null }, date: { ne: null } } }
      sort: { order: DESC, fields: [frontmatter___date] }
    ) {
      edges {
        node {
          id
          frontmatter {
            title
            date(formatString: "MMMM DD, YYYY")
            path
          }
          excerpt
        }
      }
    }
  }
`
